              <div class="p-6">
                <span class="item-circle mb-4 w-12 h-12" :style="{background: `rgba(var(--vs-primary),.15)`}">
                  <i class="far fa-users fa-lg text-primary"></i>
                </span>
                <div class="text-dark">
                  <div class="text-3xl font-bold">{{ userStats.activeUsers | currency('', 0) }}</div>
                  <span>{{ $t('ACTIVE_USERS') }}</span>
                </div>
              </div>
            </div>
          </nv-card>
        </router-link>
      </div>

      <div class="nv-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <router-link :to="{ name: 'BrowseUsers', params: { filter: 'disabled:Y' } }">
          <nv-card>
            <div slot="no-body">
              <div class="p-6">
                <span class="item-circle mb-4 w-12 h-12" :style="{background: `rgba(var(--vs-danger),.15)`}">
                  <i class="far fa-user-lock fa-lg text-danger"></i>
                </span>
                <div class="text-dark">
                  <div class="text-3xl font-bold">{{ userStats.disabledUsers | currency('', 0) }}</div>
                  <span>{{ $t('DISABLED_USERS') }}</span>
                </div>
              </div>
            </div>
          </nv-card>
        </router-link>
      </div>

      <div class="nv-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <router-link :to="{ name: 'BrowseUsers', params: { filter: 'approved:N' } }">
          <nv-card>
            <div slot="no-body">
              <div class="p-6">
                <span class="item-circle mb-4 w-12 h-12" :style="{background: `rgba(var(--vs-warning),.15)`}">
                  <i class="far fa-user-clock fa-lg text-warning"></i>
                </span>
                <div class="text-dark">
                  <div class="text-3xl font-bold">{{ userStats.pendingUsers | currency('', 0) }}</div>
                  <span>{{ $t('PENDING_USERS') }}</span>
                </div>
              </div>
            </div>
          </nv-card>
        </router-link>
      </div>

      <div class="nv-col w-full sm:w-1/2 md:w-1/2 lg:w-1/4 xl:w-1/4 mb-base">
        <nv-card>
          <div slot="no-body">
            <div class="p-6">
              <div class="flex flex-wrap-reverse flex-grow justify-between content-start mb-4">
                <div>
                  <span class="item-circle w-12 h-12" :style="{background: `rgba(var(--vs-success),.15)`}">
                    <i class="far fa-user-plus fa-lg text-success"></i>
                  </span>
                </div>
                <div>
                  <nv-time-interval-dropdown v-on:timeIntervalChanged="timeIntervalChanged"/>
                </div>
              </div>
              <div>
                <div class="text-3xl font-black">{{ userStats.newUsers | currency('', 0) }}</div>
                <span>{{ $t('NEW_USERS') }}</span>
              </div>
            </div>
          </div>
        </nv-card>
      </div>
    </div>

    <div class="nv-row">
      <div class="nv-col w-full sm:w-1/2 md:w-1/2 lg:w-1/3 xl:w-1/3 mb-base">
        <router-link :to="{ name: 'BrowseWorkflows', params: { filter: 'disabled:N AND approved:Y' } }">
          <nv-card>
            <div slot="no-body">
              <div class="p-6">
                <span class="item-circle mb-4 w-12 h-12" :style="{background: `rgba(var(--vs-primary),.15)`}">
                  <i class="far fa-shapes fa-lg text-primary"></i>
                </span>
                <div class="text-dark">
                  <div class="text-3xl font-bold">{{ workflowStats.activeWorkflows | currency('', 0) }}</div>
                  <span>{{ $t('ACTIVE_WORKFLOWS') }}</span>
                </div>
              </div>
            </div>
          </nv-card>
        </router-link>
      </div>

      <div class="nv-col w-full sm:w-1/2 md:w-1/2 lg:w-1/3 xl:w-1/3 mb-base">
        <router-link :to="{ name: 'BrowseWorkflows', params: { filter: 'disabled:Y' } }">
          <nv-card>
            <div slot="no-body">
              <div class="p-6">
                <span class="item-circle mb-4 w-12 h-12" :style="{background: `rgba(var(--vs-danger),.15)`}">
                  <i class="far fa-lock fa-lg text-danger"></i>
                </span>
                <div class="text-dark">
                  <div class="text-3xl font-bold">{{ workflowStats.disabledWorkflows | currency('', 0) }}</div>
                  <span>{{ $t('DISABLED_WORKFLOWS') }}</span>
                </div>
              </div>
            </div>
          </nv-card>
        </router-link>
      </div>

      <div class="nv-col w-full sm:w-1/2 md:w-1/2 lg:w-1/3 xl:w-1/3 mb-base">
        <router-link :to="{ name: 'BrowseWorkflows', params: { filter: 'approved:N' } }">
          <nv-card>
            <div slot="no-body">
              <div class="p-6">
                <span class="item-circle mb-4 w-12 h-12" :style="{background: `rgba(var(--vs-warning),.15)`}">
                  <i class="far fa-clock fa-lg text-warning"></i>
                </span>
                <div class="text-dark">
                  <div class="text-3xl font-bold">{{ workflowStats.pendingWorkflows | currency('', 0) }}</div>
                  <span>{{ $t('PENDING_WORKFLOWS') }}</span>
                </div>
              </div>
            </div>
          </nv-card>
        </router-link>
      </div>
    </div>

    <div class="nv-row">
      <div class="nv-col w-full sm:w-1 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
        <nv-card :title="$t('WORKFLOWS_PER_STATUS')" class="mb-base">
          <div class="mt-4">
            <nv-pie-chart :height=250 :chartData="workflowStatusChartData" :options="pieChartOptions"></nv-pie-chart>
          </div>
        </nv-card>
      </div>
      <div class="nv-col w-full sm:w-1 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-base">
        <nv-card :title="$t('WORKFLOWS_PER_TYPE')" class="mb-base">
          <div class="mt-4">
            <nv-bar-chart :height=250 :chartData="workflowTypeChartData" :options="barChartOptions"></nv-bar-chart>
          </div>
        </nv-card>
      </div>
    </div>
  </div>
</template>

<script>
import NvTimeIntervalDropdown from '@/components/nv-time-interval-dropdown/NvTimeIntervalDropdown'
import NvPieChart from '@/components/nv-chart/NvPieChart'
import NvBarChart from '@/components/nv-chart/NvBarChart'
import SecurityService from '@/services/nucleus/SecurityService'
import WorkflowService from '@/services/commons/WorkflowService'

export default {
  name: 'dashboard',
  components: {
    NvTimeIntervalDropdown,
    NvPieChart,
    NvBarChart
  },
  data: function () {
    return {
      userFromDate: null,
      userStats: {},
      workflowStats: {},
      pieChartOptions: {
        title: {
          display: false
        }
      },
      barChartOptions: {
        title: {
          display: false
        },
        legend: {
          display: false
        },
        scales: {
          yAxes: [{
            ticks: {
              min: 0,
              precision: 0
            }
          }]
        }
      }
    }
  },
  computed: {
    workflowStatusChartData: function () {
      let labels = []
      let data = []
      if (this.workflowStats.workflowStatuses) {
        for (let i = 0; i < this.workflowStats.workflowStatuses.length; i++) {
          labels.push(this.workflowStats.workflowStatuses[i].statusDescription)
          data.push(this.workflowStats.workflowStatuses[i].instanceCount)
        }
      }

      return {
        labels: labels,
        datasets: [{
          label: '',
          backgroundColor: ['#7367F0', '#28C76F', '#EA5455', '#FF9F43', '#1E1E1E', '#761FD3'],
          data: data
        }]
      }
    },
    workflowTypeChartData: function () {
      let labels = []
      let data = []
      if (this.workflowStats.workflowTypes) {
        for (let i = 0; i < this.workflowStats.workflowTypes.length; i++) {
          labels.push(this.workflowStats.workflowTypes[i].typeName)
          data.push(this.workflowStats.workflowTypes[i].instanceCount)
        }
      }

      return {
        labels: labels,
        datasets: [{
          label: '',
          backgroundColor: ['#7367F0', '#28C76F', '#EA5455', '#FF9F43', '#1E1E1E', '#761FD3'],
          data: data
        }]
      }
    }
  },
  methods: {
    async load () {
      this.loadUserStats()
      this.loadWorkflowStats()
    },
    loadUserStats () {
      if (this.userFromDate == null) {
        let today = new Date()
        this.userFromDate = new Date().setDate(today.getDate() - 7)
      }

      let self = this
      SecurityService.userStats({
        fromDate: this.userFromDate
      }).then(function (response) {
        self.userStats = response.data || {}
      })
    },
    loadWorkflowStats () {
      let self = this
      WorkflowService.workflowStats({
      }).then(function (response) {
        self.workflowStats = response.data || {}
      })
    },
    timeIntervalChanged (interval) {
      let today = new Date()
      this.userFromDate = new Date().setDate(today.getDate() - interval)
      this.loadUserStats()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
